			"regras": "Queda do PIB é momentânea e Brasil tem condições para retomada, diz Dilma\n\nA presidente Dilma Rousseff (PT) disse, nesta sexta-feira (29), que o resultado ruim do PIB (Produto Interno Bruto), apresentado nos dois últimos trimestres, é momentâneo. Ela lembrou dos feriados durante a Copa do Mundo e comparou o desempenho da economia brasileira com a de outros países.\n—  Eu acredito que esse resultado do PIB é momentâneo. É deste trimestre. Um dos motivos que explica esse resultado é o número de feriados que nós tivemos por conta da Copa do Mundo.\nEm discurso realizado em Salvador (BA), Dilma também afirmou que acredita em uma recuperação da economia já no próximo semestre.\n— Acredito que no segundo semestre nós teremos uma grande recuperação. O Brasil tem hoje todas as condições para ter hoje uma grande retomada. Nós estamos criando essas condições.\nA presidente, que é candidata à reeleição, lembrou que outros países também enfrentam problemas em suas economias.\n— Têm três países que se saíram bem do segundo trimestre no mundo. Um é a China, outro é os Estados Unidos e o outro é o Reino Unido. Nos demais países você tem uma redução drástica do crescimento, inclusive aqui na América Latina se verifica isso. Uma das explicações foi a queda no preço das commodities\nO Produto Interno Bruto do Brasil caiu 0,6% no segundo trimestre sobre os primeiros três meses do ano, segundo dados divulgados pelo IBGE nesta sexta-feira (29). Como o instituto revisou para baixo o resultado do primeiro trimestre, o País entrou em recessão técnica. \nPIB brasileiro cai 0,6% no 2º trimestre, diz IBGE\nO PIB (Produto Interno Bruto) nacional encolheu 0,6% ao longo do segundo trimestre de 2014, na comparação com o primeiro do ano. O resultado, divulgado nesta sexta-feira (29) pelo IBGE (Instituto Brasileiro de Geografia e Estatística), leva em conta todas as riquezas produzidas pelo País.O instituto também aproveitou para revisar a divulgação do resultado referente ao primeiro trimestre do ano, que passou de 0,2% para -0,2%, o que caracteriza que o Brasil agora se encontra em uma recessão técnica, situação na qual a economia encolhe por dois trimestres seguidos.\nApesar das duas quedas seguidas, no acumulado dos quatro últimos trimestres, houve crescimento de 1,4% em relação aos quatro trimestres anteriores.\nEm valores correntes, a produção das riquezas do País alcançou R 1,27 trilhão no segundo trimestre, sendo R 1,1 trilhão referentes ao valor adicionado a preços básicos e R 183,7 bilhões aos Impostos sobre Produtos líquidos de Subsídios.\n\nveja estes link tbm\nhttp://dikadeartigos.blogspot.com.br/\nhttp://dikadeartigos.blogspot.com.br/\nhttp://dikadeartigos.blogspot.com.br/",
